import React from "react";
import { gql, useQuery } from "@apollo/client";
import { Link, useParams } from "react-router-dom";
import * as dayjs from "dayjs";
import Rocket from "./Rocket";

const LAUNCH_QUERY = gql`
    query launchQuery($id: String!) {
        launch(id: $id) {
            flight_number
            name
            date_local
            success
            rocket
        }
    }
`;


const Launch = () => {
    const { id } = useParams();
    const { error, loading, data } = useQuery(LAUNCH_QUERY, {
        variables: { id },
    });

    if (loading) {
        return <h1>Loading...</h1>;
    }

    if (error) {
        console.log(error);
        return <h1>Error</h1>;
    }

    const { name, flight_number, date_local, success, rocket } = data.launch;
    const year = dayjs(date_local).format("YYYY");
    const date = dayjs(date_local).format("YYYY-MM-DD HH:MM");


    return (
        <>
            <div className="display-4 my-3">
                Mission: <span className="fw-bold">{name}</span>
            </div>
            <h4 className="mb-3"></h4>
            <ul className="list-group">
                <li className="list-group-item list-group-item-action">
                    Flight Number: {flight_number}
                </li>
                <li className="list-group-item list-group-item-action">Launch Year: {year}</li>
                <li className="list-group-item list-group-item-action">
                    Success:{" "}
                    <span className={success ? "text-success" : "text-danger"}>
                        {success ? "Yes" : "No"}
                    </span>
                </li>
                <li className="list-group-item list-group-item-action">
                    Flight Number: {flight_number}
                </li>
            </ul>
            <Rocket id={rocket}/> 
            <Link to="/" className="btn btn-primary mb-5">Back</Link>
        </>
    );
};

export default Launch;
