import React from "react";
import { gql, useQuery } from "@apollo/client";

const ROCKET_QUERY = gql`
    query rocketQuery($id: String!) {
        rocket(id: $id) {
            name
            type
            country
            height {
                meters
            }
            mass {
                kg
            }
            flickr_images
        }
    }
`;

const Rocket = ({ id }) => {
    const { error, data, loading } = useQuery(ROCKET_QUERY, {
        variables: { id },
    });

    if (loading) {
        return <h5>Loading...</h5>;
    }

    if (error) {
        console.log(error);
        return <h5>Error</h5>;
    }

    const {
        name,
        type,
        country,
        height: { meters },
        mass: { kg },
        flickr_images,
    } = data.rocket;

    const rocketIamgeUrl = flickr_images[0];

    return (
        <>
            <h4 className="my-3">Rocket Details</h4>
            <ul className="list-group my-3">
                <li className="list-group-item list-group-item-action">Name: {name}</li>
                <li className="list-group-item list-group-item-action">Type: {type}</li>
                <li className="list-group-item list-group-item-action">Country: {country}</li>
                <li className="list-group-item list-group-item-action">Height: {meters}m</li>
                <li className="list-group-item list-group-item-action">Mass: {kg}kg</li>
                <li className="list-group-item list-group-item-action">
                    Image:
                    <br />
                    <img
                        src={rocketIamgeUrl}
                        alt="Rocket"
                        className="rocket-img"
                    />
                </li>
            </ul>
        </>
    );
};

export default Rocket;
